import React, { MouseEvent, useState } from "react";
import Home from "components/home/home";
import About from "components/about/about";
import Footer from "components/footer/footer";
import ReactPageScroller from "react-page-scroller";

const SectionWipes = () => {
    const [page, setPage] = useState(0);

    const handleClick = (e: MouseEvent<HTMLButtonElement>) => {
        e.preventDefault();
        setPage(+e.currentTarget.name);
    };
    return (
        <>
            <nav>
                <ul>
                    <li>
                        <button
                            name="0"
                            onClick={handleClick}
                            className={page === 0 ? "is-dark" : ""}
                        >
                            <span className="nav-dot"></span>
                            <span className="nav-label">Intro</span>
                        </button>
                    </li>
                    <li>
                        <button
                            name="1"
                            onClick={handleClick}
                            className={page === 1 ? "is-dark" : ""}
                        >
                            <span className="nav-dot"></span>
                            <span className="nav-label">About</span>
                        </button>
                    </li>
                    <li>
                        <button
                            name="2"
                            onClick={handleClick}
                            className={page === 2 ? "is-dark" : ""}
                        >
                            <span className="nav-dot"></span>
                            <span className="nav-label">Сontacts</span>
                        </button>
                    </li>
                </ul>
            </nav>
            <ReactPageScroller
                pageOnChange={(v:number) => {
                    setPage(v);
                }}
                customPageNumber={page}
                transitionTimingFunction="ease-in"
                animationTimer={500}
            >
                <div className="section" id="home">
                    <Home />
                </div>
                <div className="section" id="about">
                    <About />
                </div>
                <div className="section" id="contacts">
                    <Footer />
                </div>
            </ReactPageScroller>
        </>
    );
};

export default SectionWipes;
